.header {
  background-size: cover;
  background-position: center;
  background-image: url(/src/images/bgimage.png);

  &__content {
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: space-between;
  }


  &__bottom {
    padding-bottom: 40px;

    @include on-tablet {
      padding-bottom: 64px;
    }
  }

  &__title {
    margin: 0;
    color: #fff;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: 0;
    text-align: left;

    @include on-tablet {
      font-size: 48px;
    }
  }

}
