.page {
  font-family: Manrope, sans-serif;
  color: $main-text-color;
  scroll-behavior: smooth;

  &:has(.page__aside-menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__aside-menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    pointer-events: none;
    transition: all 0.4s;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;

      transform: translateX(0);
    }
  }
}
